import React from "react";
import ContentLoader from "react-content-loader";

export const Skeleton = () => (
  <ContentLoader
    className="pizza-block"
    speed={2}
    width={280}
    height={466}
    viewBox="0 0 280 466"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
  >
    <circle cx="141" cy="125" r="120" />
    <rect x="0" y="260" rx="10" ry="10" width="280" height="27" />
    <rect x="0" y="307" rx="10" ry="10" width="280" height="88" />
    <rect x="131" y="415" rx="25" ry="25" width="152" height="45" />
    <rect x="0" y="415" rx="10" ry="10" width="91" height="45" />
  </ContentLoader>
);
